/* istanbul ignore file */
import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const SecondaryNavStyled = styled.nav`
  background: rgba(255, 255, 255, 1);
  display: none;

  @media only screen and (min-width: 852px) {
    display: flex;
    align-items: center;
  }
`;

const SecondaryNavLinkStyled = styled.a`
  margin: 0 10px;
  padding: 5px 0;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  color: rgb(0, 0, 0);
  -webkit-font-smoothing: antialiased;
  border: 0 solid rgb(210, 66, 66);
  border-width: ${props => (props.isActive ? '0 0 2px 0' : '0')};
`;

function SecondaryNav({ navItems, activeIndex }) {
  return (
    <SecondaryNavStyled>
      {navItems &&
        navItems.map(({ cta }, index) => {
          const { text, link } = cta;
          return (
            <SecondaryNavLinkStyled
              isActive={index === activeIndex}
              key={index}
              href={link}
            >
              {text}
            </SecondaryNavLinkStyled>
          );
        })}
    </SecondaryNavStyled>
  );
}

SecondaryNav.propTypes = {
  navItems: PropTypes.arrayOf(PropTypes.object),
  activeIndex: PropTypes.number
};

export default SecondaryNav;
