/* istanbul ignore file */
import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const PrimaryNavLinkStyled = styled.a`
  text-transform: uppercase;
  margin: 0 10px;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  letter-spacing: 0.4px;
  line-height: 24px;
  color: ${prop => (prop.isActive ? 'white' : '#888a8c')};
  -webkit-font-smoothing: antialiased;
`;
const DesktopNav = styled.div`
  display: flex;

  @media only screen and (max-width: 852px) {
    display: none;
    align-items: center;
  }
`;
const MobileNav = styled.div`
  display: none;
  @media only screen and (max-width: 852px) {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
  }
`;
const LogoImage = styled.img`
  width: 90px;
`;

function HeaderPrimaryNav({ navItems = [], activeIndex = 0 }) {
  return (
    <nav>
      <DesktopNav>
        {navItems.map(({ cta }, index) => {
          const { text, link } = cta;
          return (
            <PrimaryNavLinkStyled
              key={index}
              href={link}
              isActive={activeIndex === index}
            >
              {text}
            </PrimaryNavLinkStyled>
          );
        })}
        <a href="https://www.optus.com.au/">
          <img
            src="data:image/png;base64,"
            id="optus_logo_headImg"
            className="optus_logo_headImg"
            alt="Optus logo"
          />
        </a>
      </DesktopNav>
      <MobileNav>
        <LogoImage src="https://beta-cdn.optusdigital.com/content/dam/optus/images/about-us/media-centre/multimedia/logos/OPTUS_Yellow_sRGB_RELEASE_03_310316.png/renditions/version-1506405814053/original.png" />
      </MobileNav>
    </nav>
  );
}

HeaderPrimaryNav.propTypes = {
  navItems: PropTypes.arrayOf(PropTypes.object),
  activeIndex: PropTypes.number
};

export default HeaderPrimaryNav;
