/* istanbul ignore file */
import React from 'react';
import PropTypes from 'prop-types';

import styled from 'styled-components';
import HeaderPrimaryNav from './HeaderPrimaryNav';
import HeaderSecondaryNav from './HeaderSecondaryNav';

const Wrapper = styled.div`
  z-index: 999;
  width: 100%;
  @media only screen and (min-width: 1020px) {
    position: relative;
    top: 1.25rem;
    width: 100%;
    z-index: 1;
  }
`;

const Grid = styled.div`
  width: 65rem;
  max-width: 100%;
  padding: 0 20px;
  margin: 0 auto 40px;

  @media only screen and (max-width: 852px) {
    margin-bottom: 40px;
    padding: 0;
  }
`;

const HeaderTop = styled.div`
  display: flex;
  height: 42px;
  align-items: center;
  justify-content: space-between;
  background: rgba(0, 0, 0, 1);

  @media only screen and (max-width: 852px) {
    height: 52px;
  }
`;

/**
 * Get active primary nav item
 */
function getActivePrimaryNavItemIndex(
  currentPageRootKey,
  currentPageRootUrl,
  navigationModel
) {
  const rootUrl =
    currentPageRootUrl || window.localStorage.getItem(currentPageRootKey) || '';
  const activeIndex = navigationModel.childNavItems.findIndex(item => {
    return item.cta.link === rootUrl;
  });
  return activeIndex > -1 ? activeIndex : 0;
}

/**
 * Get the secondary selected index
 */
function getSecondaryActiveNavItem(
  navItems,
  currentPageTopParentKey,
  currentPageTopParentUrl
) {
  const topParentUrl =
    currentPageTopParentUrl ||
    localStorage.getItem(currentPageTopParentKey) ||
    '';
  const activeIndex = navItems.findIndex(({ cta }) => {
    return cta.link === topParentUrl;
  });
  return activeIndex > -1 ? activeIndex : 0;
}

/**
 * Filter top level navigation
 */
function filterMenuItems(items) {
  return items.filter(({ microsite, active }) => {
    if (microsite) {
      return active;
    } else {
      return true;
    }
  });
}

function Header({
  currentPageRootKey = '',
  currentPageRootUrl = '',
  currentPageTopParentKey = '',
  currentPageTopParentUrl = '',
  navigationModel = {}
}) {
  const primaryActiveIndex = getActivePrimaryNavItemIndex(
    currentPageRootKey,
    currentPageRootUrl,
    navigationModel
  );
  const secondaryNavItems =
    navigationModel.childNavItems[primaryActiveIndex].childNavItems;
  const secondaryActiveIndex = getSecondaryActiveNavItem(
    secondaryNavItems,
    currentPageTopParentKey,
    currentPageTopParentUrl
  );

  return (
    <Wrapper>
      <Grid>
        <header>
          <HeaderTop>
            <HeaderPrimaryNav
              activeIndex={primaryActiveIndex}
              navItems={filterMenuItems(navigationModel.childNavItems)}
            />
          </HeaderTop>
          <HeaderSecondaryNav
            navItems={secondaryNavItems}
            activeIndex={secondaryActiveIndex}
          />
        </header>
      </Grid>
    </Wrapper>
  );
}

Header.propTypes = {
  currentPageRootKey: PropTypes.string,
  currentPageRootUrl: PropTypes.string,
  currentPageTopParentKey: PropTypes.string,
  currentPageTopParentUrl: PropTypes.string,
  navigationModel: PropTypes.object
};

export default Header;
